<template>
  <div class="post-wrap">
    <h3>美的研究所 Landing Page 活動區塊</h3>

    <h4>2020 Oyaslim LINE 貼紙活動說明</h4>

    <ul class="block-wrap">
        <li>使用素材：美的研究所 Landing Page</li>
        <li>製作：Photoshop</li>
    </ul>

    <h5>電腦版</h5>
    <!-- image: 1 column 100% width -->
    <div class="img-wrap">
        <img src="@/assets/images/work/fordel_lp/line_pc.png" />
    </div>

    <h5>手機版</h5>
    <!-- image: 1 column 450px width -->
    <div class="img-wrap column-1">
        <img src="@/assets/images/work/fordel_lp/line_sp.png" />
    </div>


    <h4>2021 AFTEE 活動說明</h4>
    <ul class="block-wrap">
        <li>使用素材：AFTEE 視覺圖</li>
        <li>製作：HTML、CSS、jQuery</li>
    </ul>

    <!-- image: 1 column 100% width -->
    <div class="img-wrap">
        <img src="@/assets/images/work/fordel_lp/aftee_2021.png" />
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'WdFordelLp',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

